import React, { useEffect, useState } from 'react';
import useAuth from '../../hooks/useAuth';

const MyOrders = () => {
    const { user } = useAuth();
    const [orders, setOrders] = useState([]);

    useEffect(() => {
        const url = `https://infinite-river-22082.herokuapp.com/orders?email=${user.email}`;
        fetch(url)
            .then(res => res.json())
            .then(data => setOrders(data))
    }, []);

    const handleDeleteOrder = id => {
        const proceed = window.confirm('Are You Sure You Wanna Delete?');
        if (proceed) {
            fetch(`https://infinite-river-22082.herokuapp.com/orders/${id}`, {
                method: 'DELETE'
            })
                .then(res => res.json())
                .then(data => {
                    if (data.deletedCount > 0) {
                        alert('Order Deleted!');
                        const remaining = orders.filter(order => order._id !== id);
                        setOrders(remaining);
                    }
                })
        }
    }
    return (
        <div>
            <h1 className='title text-center m-4'>My Orders</h1>
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Name</th>
                        <th scope="col">Car</th>
                        <th scope="col">Action</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        orders.map(order => <tr>
                            <td>{order.userName}</td>
                            <td>{order.carName}</td>
                            <td><button onClick={() => handleDeleteOrder(order._id)} style={{ border: 'none', backgroundColor: 'black', color: 'white' }}>Delete âŒ</button></td>
                        </tr>
                        )
                    }
                </tbody>
            </table>



        </div>
    );
};

export default MyOrders;