import React, { useEffect, useState } from 'react';

const ManageProducts = () => {
    const [cars, setCars] = useState([]);
    useEffect(() => {
        fetch('https://infinite-river-22082.herokuapp.com/cars')
            .then(res => res.json())
            .then(data => setCars(data))
    }, []);

    const handleDeleteCar = id => {
        const proceed = window.confirm('Are You Sure You Wanna Delete The Product?');
        if (proceed) {
            fetch(`https://infinite-river-22082.herokuapp.com/cars/${id}`, {
                method: 'DELETE'
            })
                .then(res => res.json())
                .then(data => {
                    if (data.deletedCount > 0) {
                        const remaining = cars.filter(car => car._id !== id);
                        setCars(remaining);
                    }
                })
        }
    }
    return (
        <div>
            <h1 className='title text-center m-4'>My Cars ðŸš—</h1>
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Car Name</th>
                        <th scope="col">Action</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cars.map(car => <tr>
                            <td>{car.name}</td>
                            <td><button onClick={() => handleDeleteCar(car._id)} style={{ border: 'none', backgroundColor: 'black', color: 'white' }}>Delete âŒ</button></td>
                        </tr>
                        )
                    }
                </tbody>
            </table>



        </div>
    );
};

export default ManageProducts;