import React from 'react';
import { useForm } from 'react-hook-form';
import { Link, useLocation, useHistory } from 'react-router-dom';
import useAuth from '../../hooks/useAuth';
import Navbar from '../Shared/Navbar/Navbar';
import google from '../../assets/images/google.png';

const Login = () => {
    const { register, handleSubmit } = useForm();
    const { loginUser, signInWithGoogle } = useAuth();
    const location = useLocation();
    const history = useHistory();
    const onSubmit = data => {
        loginUser(data.email, data.password, location, history);
    };

    const handleGoogleSignIn = () => {
        signInWithGoogle(location, history);
    }
    return (
        <div>
            <Navbar />
            <div style={{ padding: '100px' }} className='container'>
                <h2 className='title text-center'>Welcome Back, Riders!</h2>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <input style={{ fontFamily: 'Poppins' }} className='m-3 w-50' {...register("email")} placeholder=' Email' />
                    <br />
                    <input style={{ fontFamily: 'Poppins' }} className='m-3 w-50' {...register("password")} type='password' placeholder=' Password' />
                    <br />
                    <input style={{ backgroundColor: 'black', color: 'white', fontFamily: 'Poppins', border: 'none' }} className='m-3 w-25' type="submit" value='Login' />
                </form>
                <h5 style={{ fontFamily: 'Poppins' }} className='m-3'>New Here? Please <Link style={{ color: 'blue' }} to='/register'>Register</Link> </h5>
                <br />
                <p style={{ fontFamily: 'Poppins' }} className='m-3'>or</p>
                <br />
                <button style={{ backgroundColor: 'black', color: 'white', border: 'none', padding: '20px' }} onClick={handleGoogleSignIn}> <img style={{ width: '20px' }} src={google} alt="" />  Google Sign In</button>
            </div>
        </div>
    );
};

export default Login;