import React, { useEffect, useState } from 'react';
import HomeCars from '../../HomeCars/HomeCars';
import Reviews from '../../Reviews/Reviews';
import Navbar from '../../Shared/Navbar/Navbar';
import FAQ from '../FAQ/FAQ';
import HomeBanner from '../HomeBanner/HomeBanner';
import './Home.css';

const Home = () => {
    const [cars, setCars] = useState([]);
    useEffect(() => {
        fetch('https://infinite-river-22082.herokuapp.com/cars')
            .then(res => res.json())
            .then(data => setCars(data));
    }, []);

    const allCars = cars.slice(0, 6);
    return (
        <div>
            <Navbar />
            <HomeBanner />
            <h2 style={{ marginTop: '200px' }} className='text-center title'>Our Premium Cars</h2>
            <div className='container'>
                <div className='cars'>
                    {
                        allCars.map(car => <HomeCars
                            key={car._id}
                            car={car}
                        ></HomeCars>)
                    }
                </div>
            </div>
            <Reviews />
            <FAQ />
        </div>
    );
};

export default Home;